<template>
  <div class="certification">
    <div class="tips">输入验证码查看证件信息</div>
    <div class="input-wrap">
      <div class="left">请输入验证码：</div>
      <div class="right">
        <input placeholder="请输入验证码" type="text" v-model="imgCaptcha" />
        <img :src="imgCaptchaUrl" alt />
      </div>
    </div>
    <div class="btn-wrap">
      <div @click="fnConfirm" class="btn">确定</div>
    </div>
  </div>
</template>

<script>
import config from '@/config'
import { enterprise } from '@/http/service/shop'
export default {
  data() {
    return {
      imgCaptchaUrl: '',
      imgCaptcha: '',
      ownerId: '',
    }
  },
  created() {
    this.ownerId = this.$route.query.ownerId
    // this.ownerId = '1821112663'
    let imgCaptchaToken = encodeURIComponent(this.$store.state.sessionValue)
    console.log(imgCaptchaToken)
    this.imgCaptchaUrl = `${config.HOST}/v3/platform/web/member/register/getImgCaptcha.html?flag=Bearer` + imgCaptchaToken + `&rnd=0.${this.randomn(10)}`
  },
  methods: {
    async fnConfirm() {
      if (this.imgCaptcha === '') {
        this.$toast('请输入验证码')
        return
      }
      const _resData = await this.fnGetEnterprise()
      this.$router.push({
        path: '/shopLicense',
        query: {
          resData: _resData,
        },
      })
    },
    /**
     * @description 获取商家营业执照
     */
    fnGetEnterprise() {
      return new Promise((resolve, reject) => {
        enterprise({
          // channel: 1,
          ownerId: this.ownerId,
          imgCaptcha: this.imgCaptcha,
        }).then(res => {
          if (res.success) {
            resolve(res.data)
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 随机数
     */
    randomn(n) {
      if (n > 15) return null
      return parseInt((Math.random() + 1) * Math.pow(10, n - 1))
    }
  },
}
</script>

